/** @property $errors */

/** @property entity */

/** @method update */

/** @method store */
export default {
  name: "AttachmentForm",
  beforeCreate: function beforeCreate() {
    this.$bind.mapGetters('Attachments', {
      entity: 'entity'
    });
    this.$bind.mapActions('Attachments', {
      isLoading: 'isLoading',
      update: 'update',
      store: 'store'
    });
    this.$bind.mapState('Attachments', {
      $errors: '$errors'
    });
  },
  created: function created() {
    this.$errors.clear();
  },
  beforeDestroy: function beforeDestroy() {
    this.$errors.clear();
  }
};